@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap);
:root {
  /* Color */
  --color-white: #ffffff;
  --color-light-white: #eeeeee;
  --color-dark-white: #bdbdbd;
  --color-pink: #fe918d;
  --color-dark-pink: #ff6863;
  --color-black: #000000;
  --color-dark-grey: #4d4d4d;
  --color-grey: #7e7e7e;
  --color-light-grey: #9e9e9e;
  --color-blue: #73aace;
  --color-yellow: #fff7d1;
  --color-light-orange: #fdde70;
  --color-orange: #feb546;
  --color-deep-orange: #ff9900;
  --color-blue: #5ea7fa;
  --color-deep-blue: #0026ff;

  /* Font size */
  --font-very-large: 50px;
  --font-large: 38px;
  --font-medium: 24px;
  --font-regular: 18px;
  --font-small: 16px;
  --font-micro: 12px;
  --font-nano: 10px;

  /* Font Weight */
  --weight-bold: 700;
  --weight-semi-bold: 600;
  --weight-regular: 400;

  /* Size */
  --size-border-radius: 4px;

  /* Animation */
  --animation-duration: 300ms;
}

/* Universal tags */
* {
  box-sizing: border-box;
}

/* font */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: default;
}

a {
  text-decoration: none;
  color: black;
}

ul {
  list-style: none;
  padding-left: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  font-size: 44px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.subTitle {
  color: #868e96;
}

#navbar {
  position: fixed;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  padding: 12px;
  transition: all var(--animation-duration) ease-in-out;
  z-index: 2;
}

.navbar__menu {
  display: flex;
  margin: 4px 0px;
}

.navbar__logo__title {
  display: inline-block;
  padding: 6px 12px;
  color: var(--color-black);
  font-size: var(--font-large);
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 500;
}

.navbar__menu__item {
  padding: 8px 12px;
  margin: 0 10px;
  color: var(--color-black);
  border-radius: var(--size-border-radius);
  cursor: pointer;
  border: 1px solid transparent;
  font-size: var(--font-regular);
  font-weight: 600;
}

@media screen and (max-width: 768px) {
  #navbar {
    flex-direction: column;
    align-items: flex-start;
  }
  .navbar__menu {
    flex-direction: column;
    text-align: center;
    width: 100%;
    /* display: none; */
  }
}

.academy__main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  margin-bottom: 30px;
}

.academy__container {
  width: 85%;
}

.academy__text {
  text-align: left;
}

.academy__card {
  display: flex;
  flex-direction: row;
  padding: 16px 0;
  align-items: center;
  transition: all var(--animation-duration) ease;
}

.academy__card__left {
  display: flex;
  align-items: center;
  width: 150px;
  height: 150px;
}

.academy__card:hover + .academy__card__border {
  border-color: var(--color-deep-orange);
  width: 100%;
  transition: width 0.6s ease-in-out;
}
.academy__card__left > img {
  max-width: 100%;
}

.academy__card_right {
  text-align: left;
  padding-left: 3rem;
}

.academy__Name {
  font-size: var(--font-medium);
}

.academy__desc {
  font-size: var(--font-regular);
}

.academy__duration {
  font-size: var(--font-small);
}

.academy__card__border {
  height: 20px;
  width: 80%;
  border-top: 3.5px solid rgba(211, 211, 211, 0.397);
  margin-bottom: 10px;
  transition: width 0.6s ease-in-out;
}

@media screen and (max-width: 768px) {
  .academy__card__left {
    width: 100px;
    height: 100px;
  }

  .academy__card_right {
    padding-left: 1.5rem;
  }

  .academy__Name {
    font-size: var(--font-small);
  }

  .academy__desc {
    font-size: var(--font-micro);
  }

  .academy__duration {
    font-size: var(--font-nano);
  }
}

.achievements {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

.achievementCard__container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 280px;
  justify-content: center;
  align-items: center;
  margin: 30px 40px;
}

.achievementCard__image {
  width: 100%;
  height: auto;
}

.achievementCard__image__div {
  position: relative;
  transform: scale(1);
  -webkit-transform: scale(1);
  -moz-transform: scale(1);
  -ms-transform: scale(1);
  -o-transform: scale(1);
  transition: all 0.3s ease-in-out; /* 부드러운 모션을 위해 추가*/
}

.achievementCard__image__div:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
}

.achievementCard__image:hover {
  box-shadow: rgba(0, 0, 0, 0.2) 0px 20px 30px -10px;
}

.achievementCard__title {
  word-break: keep-all;
}

@media screen and (max-width: 768px) {
  .achievementCard__container {
    width: 200px;
    height: auto;
    margin: 30px 20px;
  }
}

.go-corner {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 32px;
  height: 32px;
  overflow: hidden;
  top: 0;
  right: 0;
  background-color: #3d8bff;
  border-radius: 0 4px 0 32px;
}

.go-arrow {
  margin-top: -4px;
  margin-right: -4px;
  color: white;
  font-family: courier, sans;
}

.blogCard {
  display: block;
  position: relative;
  max-width: 100%;
  background-color: #ffffff;
  border-radius: 4px;
  padding: 32px 24px;
  margin: 12px;
  text-decoration: none;
  z-index: 0;
  overflow: hidden;
  box-shadow: 2px 2px 15px black;
}
.blogCard:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: -16px;
  right: -16px;
  background: #ffbf49;
  height: 32px;
  width: 32px;
  border-radius: 32px;
  transform: scale(1);
  transform-origin: 50% 50%;
  transition: transform 0.25s ease-out;
}
.blogCard:hover:before {
  transform: scale(50);
}

.blogCard:hover p {
  transition: all 0.3s ease-out;
  color: rgba(255, 255, 255, 0.8);
}
.blogCard:hover h3 {
  transition: all 0.3s ease-out;
  color: #ffffff;
}

@media screen and (max-width: 768px) {
  .blogCard {
    max-width: 400px;
  }
}

#blog {
  display: flex;
  justify-content: center;
  align-items: center;
}
#blog > * {
  flex: 1 1;
}

.blog__container {
  text-align: left;
  padding: 5rem;
}

.blog__cardList {
  margin-top: 3rem;
}

.blog__text {
  margin-top: 0;
}

@media screen and (max-width: 768px) {
  #blog {
    flex-direction: column;
  }
  .blog__container {
    padding: 3rem;
  }
}

#main {
  margin-top: 0;
}
#greeting {
  width: 100%;
  padding: 20px 10px;
  margin: 0px auto;
  padding-top: 4rem;
  position: relative;
  z-index: 1;
}

#greeting:after {
  background-image: url(/static/media/GreetingBackground.effbbb5c.jpeg);
  background-position: center;
  background-repeat: no-repeat;
  top: 0;
  left: 0;
  position: absolute;
  background-size: cover;
  opacity: 0.3 !important;
  z-index: -1;
  filter: alpha(opacity=50);
  content: "";
  width: 100%;
  height: 100%;
}

.greeting__main {
  width: 100%;
  margin-top: 5rem;
  display: flex;
  margin-bottom: 30px;
}
.greeting__main > * {
  flex: 1 1;
}

.greeting__text {
  width: 80%;
  font-size: var(--font-large);
  word-break: keep-all;
}
.greeting__text__container {
  padding: 30px;
  padding-left: 80px;
  display: flex !important;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.greeting__text__subtitle {
  color: #000000 !important;
  font-size: var(--font-regular);
  line-height: 40px;
  font-weight: 600;
}

.greeting__text__button {
  display: flex;
  margin-top: 20px;
  justify-content: center;
}

.greeting__img__container {
  padding: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.greeting__img__container > img {
  max-width: 50%;
  height: auto;
  border-radius: 100%;
}

@media screen and (max-width: 768px) {
  .greeting__text__container {
    padding: 30px !important;
  }
  .greeting__text__button {
    justify-content: space-around;
  }
  .greeting__text {
    font-size: var(--font-regular);
    text-align: center;
  }
  .greeting__text__subtitle {
    font-size: var(--font-small);
  }
  .greeting__main {
    display: block;
  }
  .greeting__img__container {
    height: 256px;
  }
  .greeting__img__container > img {
    height: 100%;
  }
}

@media screen and (max-width: 320px) {
  #greeting {
    width: auto;
  }
}

.main-button {
  background-color: var(--color-orange);
  color: white;
  border-radius: 4px;
  font-size: var(--font-regular);
  cursor: pointer;
  transition: all var(--animation-duration) ease-in-out;
  margin: 0px 10px;
  padding: 5px 10px;
  font-weight: 500;
}

.main-button:hover {
  background-color: #ffffff;
  color: var(--color-deep-orange);
  transition: all var(--animation-duration) ease;
  transform: translateY(-3px);
}

@media screen and (max-width: 768px) {
  .main-button {
    font-size: var(--font-small);
    padding: 12px 18px;
  }
}

@media screen and (max-width: 320px) {
  .main-button {
    font-size: var(--font-micro);
  }
}

.projects__main {
  padding: 30px;
}

.projects__categories {
  display: flex;
  justify-content: center;
}

.category__btn {
  background-color: var(--color-orange);
  color: white;
  font-size: var(--font-medium);
  cursor: pointer;
  transition: all var(--animation-duration) ease-in-out;
  margin: 0px 10px;
  padding: 10px 45px;
  font-weight: 500;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  border-radius: 8px;
  display: inline-block;
}
.category__btn > p {
  margin: 0;
}

.category__btn:hover {
  display: block;
  background-color: #ffffff;
  color: var(--color-deep-orange);
  transition: all var(--animation-duration) ease;
  transform: translateY(-3px);
  box-shadow: 0 0 0 3px var(--color-deep-orange) inset;
}

.category__btn__name {
  display: block;
}

.category__btn__cnt {
  position: absolute;
  top: 5px;
  right: 8px;
  width: 24px;
  height: 24px;
  display: inline-block;
  background-color: rgb(36, 156, 255);
  border-radius: 50%;
  line-height: 24px;
  font-size: var(--font-regular);
  transition: all var(--animation-duration);
  color: white;
}

.projects__cardList {
  padding: 2rem 3rem;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

@media screen and (max-width: 768px) {
  .projects__cardList {
    padding: 0px;
    margin-top: 30px;
  }
}

.projectCard {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  opacity: 1;
  transition: all var(--animation-duration) ease-out;
}

.projectCard.anim-out {
  opacity: 0.6;
  transform: scale(0.93) translateY(40px);
}

.projectCard__link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 480px;
  height: 420px;
  margin: 0 30px;
}

.projectCard__link.invisible {
  display: none;
}

.project__img {
  max-width: 100%;
  max-height: 100%;
  box-shadow: 3px 3px 20px black;
}

.project__info {
  position: absolute;
  background-color: #000000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 16px;
  opacity: 0;
  transform: translateY(12px);
  transition: all var(--animation-duration);
}

.projectCard__link:hover .project__info {
  opacity: 0.9;
  transform: translateY(0);
}

.project__name {
  font-size: var(--font-medium);
  font-weight: 700;
}

.project__info p {
  color: var(--color-orange);
  font-weight: 500;
  font-size: var(--font-regular);
}

.project__info p::after {
  content: "";
  display: block;
  position: relative;
  left: 50%;
  width: 50px;
  height: 2px;
  transform: translateX(-50%);
  margin-top: 8px;
  background-color: var(--color-dark-white);
}

.project__desc {
  font-size: var(--font-medium);
}

@media screen and (max-width: 768px) {
  .projectCard__link {
    width: 100%;
    height: auto;
  }

  .project__name {
    font-size: var(--font-medium);
  }
}

.dev-icons {
  padding-left: 0;
  list-style: none;
  font-size: 5rem;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
}
.software__skills__item {
  display: inline-block;
  margin-right: 20px;
  margin-bottom: 20px;
}

.software__skills__item > i {
  color: #868e96;
}

.software__skills__item > i:hover {
  color: var(--color-deep-orange);
}
.software__skills__item > p {
  color: #868e96;
  font-size: var(--font-regular);
}
.software__skills__item > i:hover ~ p {
  color: var(--color-deep-orange);
}

.skills__main {
  display: flex;
}

.skills__main > * {
  flex: 1 1;
  margin-bottom: 30px;
}

.skills__img {
  display: flex;
  justify-content: center;
  align-items: center;
}

.skills__img > img {
  max-width: 100%;
  height: auto;
  padding: 2rem;
}

.skills__subtitle {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

.skills__subtitle__text {
  font-size: var(--font-regular);
}

.subtitle__text {
  font-size: var(--font-small);
}

@media screen and (max-width: 768px) {
  .skills__main {
    flex-direction: column;
  }

  .skills__main > * {
    margin-bottom: 10px;
  }

  .skills__img > img {
    max-width: 100%;
    height: auto;
    padding: 0;
  }

  .skills__subtitle__text {
    font-size: var(--font-small);
  }
  .dev-icons {
    font-size: 3rem;
  }
  .subtitle__text {
    font-size: var(--font-micro);
  }
}

.skillsProgress {
  display: flex;
  margin-bottom: 30px;
}

.skillsProgress > * {
  flex: 1 1;
}

.progress__container {
  padding: 50px;
  margin: 0 40px;
}

.meter {
  height: 20px;
  position: relative;
  background: rgb(243, 239, 239);
  border-radius: 25px;
}

.meter > span {
  display: block;
  height: 100%;
  border-top-right-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: var(--color-deep-orange);
  position: relative;
  overflow: hidden;
}
.skill__text {
  display: flex;
  justify-content: space-between;
  font-size: var(--font-small);
}

.skill p {
  text-align: left;
}

.progressImg {
  display: flex;
  justify-content: center;
  align-items: center;
}

.progressImg > img {
  max-width: 100%;
  height: auto;
  padding: 2rem;
}

@media screen and (max-width: 768px) {
  .skillsProgress {
    flex-direction: column;
  }

  .progress__container {
    padding: 0;
    margin: 0 40px;
  }

  .progressImg {
    height: 300px;
  }

  .meter {
    height: 15px;
  }

  .progressImg > img {
    padding-top: 50px !important;
    height: 100%;
    padding: 0;
  }
}

.contact__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contact__container > * {
  flex: 1 1;
  margin-top: 2rem;
}

.contact__header__title {
  font-size: 65px;
  font-weight: 400;
}

.contact-detail,
.contact-detail-email {
  margin-top: 20px;
  font-size: 40px;
  font-weight: 400;
  color: #868e96;
  text-decoration: none;
}

.contact-detail:hover,
.contact-detail-email:hover {
  color: black;
  text-shadow: 2px 1px 2px #b5b5b5;
  transition: all 0.3s;
}

.contact__header__subtitle {
  text-transform: uppercase;
}

.contact__text {
  margin-top: 1.5rem;
}

.contact__image__div > img {
  max-width: 100%;
  height: auto;
  margin-left: 1.5rem;
  margin-top: -4rem;
}

.sk-circle {
  margin: 100px auto;
  width: 40px;
  height: 40px;
  position: relative;
}
.sk-circle .sk-child {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.sk-circle .sk-child:before {
  content: '';
  display: block;
  margin: 0 auto;
  width: 15%;
  height: 15%;
  background-color: #333;
  border-radius: 100%;
  animation: sk-circleBounceDelay 1.2s infinite ease-in-out both;
}
.sk-circle .sk-circle2 {
  transform: rotate(30deg); }
.sk-circle .sk-circle3 {
  transform: rotate(60deg); }
.sk-circle .sk-circle4 {
  transform: rotate(90deg); }
.sk-circle .sk-circle5 {
  transform: rotate(120deg); }
.sk-circle .sk-circle6 {
  transform: rotate(150deg); }
.sk-circle .sk-circle7 {
  transform: rotate(180deg); }
.sk-circle .sk-circle8 {
  transform: rotate(210deg); }
.sk-circle .sk-circle9 {
  transform: rotate(240deg); }
.sk-circle .sk-circle10 {
  transform: rotate(270deg); }
.sk-circle .sk-circle11 {
  transform: rotate(300deg); }
.sk-circle .sk-circle12 {
  transform: rotate(330deg); }
.sk-circle .sk-circle2:before {
  animation-delay: -1.1s; }
.sk-circle .sk-circle3:before {
  animation-delay: -1s; }
.sk-circle .sk-circle4:before {
  animation-delay: -0.9s; }
.sk-circle .sk-circle5:before {
  animation-delay: -0.8s; }
.sk-circle .sk-circle6:before {
  animation-delay: -0.7s; }
.sk-circle .sk-circle7:before {
  animation-delay: -0.6s; }
.sk-circle .sk-circle8:before {
  animation-delay: -0.5s; }
.sk-circle .sk-circle9:before {
  animation-delay: -0.4s; }
.sk-circle .sk-circle10:before {
  animation-delay: -0.3s; }
.sk-circle .sk-circle11:before {
  animation-delay: -0.2s; }
.sk-circle .sk-circle12:before {
  animation-delay: -0.1s; }

@keyframes sk-circleBounceDelay {
  0%, 80%, 100% {
    transform: scale(0);
  } 40% {
    transform: scale(1);
  }
}

